.item-form-wrapper {
  position: relative;
  z-index: 0;
  width: 45%;
  height: 40%;
  background-color: #FFF;
  border-radius: 10px;
  -webkit-box-shadow: 0 10px 18px rgba(0, 0, 0, 0.2), 0 14px 45px rgba(0, 0, 0, 0.2);
          box-shadow: 0 10px 18px rgba(0, 0, 0, 0.2), 0 14px 45px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.item-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 30px;
}

.btn-submit {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: end;
      align-self: flex-end;
  -ms-flex-pack: center;
      justify-content: center;
  width: 80px;
  padding: 10px;
  background-color: #25CAA6;
  border: none;
  border-radius: 10px;
  color: #FFF;
  font-size: 16px;
  font-family: 'Permanent Marker', cursive;
  letter-spacing: 2px;
}

.btn-submit:hover {
  opacity: 0.5;
}

.btn-submit:focus {
  outline: none;
}


