  outline: none;
}

.open .door {
  padding-bottom: 30px;
  -webkit-transform: translateY(-90%);
      -ms-transform: translateY(-90%);
          transform: translateY(-90%);
}

.open .open-btn {
  height: 100px;
  padding-top: 0px;
  padding-bottom: 40px;
  -webkit-animation: none;
          animation: none;
  background-position: 50% 75%;
